* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-image: url("wallpaperDaytime.b645f609.webp");
  background-size: cover;
  font-family: cursive;
}

.page--login {
  text-align: center;
}

.version_line {
  letter-spacing: .1vw;
  font-size: 1.1vw;
  position: absolute;
  top: 2%;
  left: 2%;
}

.attribution_line {
  color: #0359b6;
  font-size: 1.05vw;
}

.attribution_line--login {
  position: absolute;
  top: 2%;
  right: .4%;
}

.attribution_line--main, .attribution_line--settings {
  padding-bottom: .7%;
}

.btn--app_explanation {
  color: #000;
  cursor: pointer;
  aspect-ratio: 1 / .5;
  letter-spacing: .1vw;
  z-index: 0;
  white-space: nowrap;
  background: url("cloudSun.0de04899.svg") center / cover no-repeat;
  border: none;
  width: 16vw;
  padding-top: 1.4vw;
  padding-right: 1.5vw;
  font-family: cursive;
  font-size: 1.4vw;
  transition: all .2s;
  position: fixed;
  bottom: 4vh;
  left: 2vw;
}

.btn--app_explanation:hover {
  transform: translateY(-.3vh);
}

.btn_app_explanation--about_this_web {
  font-size: 1.2vw;
}

.login h3 {
  letter-spacing: .15vw;
  word-spacing: .3vw;
  color: #000000ad;
  margin-top: 8vh;
  font-size: 2.5vw;
}

.login h1 {
  letter-spacing: .3vw;
  word-spacing: .65vw;
  margin-top: 2vw;
  font-size: 2.8vw;
}

.login--form {
  aspect-ratio: 1 / .63;
  color: #f0f8ff;
  z-index: 2;
  background-image: linear-gradient(#fca674, #ff0);
  border-radius: 3% / 4.5%;
  flex-direction: column;
  align-items: center;
  width: 35vw;
  display: flex;
  position: relative;
  top: 3.3vw;
  left: 32vw;
  box-shadow: 4px 4px 4px #0000004d;
}

.login--form h2 {
  letter-spacing: .2vw;
  margin-top: 5%;
  font-size: 2.2vw;
}

#login--input_username, #login--input_password {
  text-align: center;
  letter-spacing: .1vw;
  border-color: #bbb;
  border-radius: 2.8% / 20%;
  width: 52%;
  height: 8.6%;
  margin-top: 8.4%;
  font-size: .9vw;
}

.btn--password_visibility {
  color: #f0f8ff;
  cursor: pointer;
  aspect-ratio: 1;
  background: url("eye.1ac89e70.svg") 0 0 / 98% no-repeat;
  border: none;
  font-family: cursive;
  transition: all .2s;
  position: absolute;
}

.btn_password_visibility--login_form {
  width: 4%;
  top: 58.3%;
  right: 25.5%;
}

.password--visible {
  background-image: url("eyeOff.4ce8eed1.svg");
}

.login--btn {
  color: #f0f8ff;
  cursor: pointer;
  background: url("sun.3e3763dd.svg") 0 0 / cover no-repeat;
  border: none;
  width: 15%;
  height: 20%;
  margin-top: 4%;
  font-family: cursive;
  transition: all .2s;
}

.login--btn:hover {
  transform: translateY(-.3vh);
}

.login--error {
  color: #f0f8ff;
  text-align: center;
  background-color: #fa7304;
  border-radius: 2% / 12%;
  padding: 3%;
  font-size: 1.3vw;
  position: absolute;
  top: 6%;
  box-shadow: 2px 2px 2px #0d0e4438;
}

.link--forgot_password {
  background-color: none;
  color: #b400b4;
  cursor: pointer;
  letter-spacing: .2vw;
  word-spacing: .3vw;
  border-bottom: thin solid;
  font-family: cursive;
  font-size: 1vw;
  transition: all .2s;
  position: relative;
  top: 4.3vw;
}

.link--forgot_password:hover {
  color: #ff25ff;
}

.scroll--explanation {
  letter-spacing: .2vw;
  color: #f0f8ff;
  margin-top: 9.6vw;
  font-size: 1.6vw;
}

.btn_scroll {
  color: #f0f8ff;
  cursor: pointer;
  background-color: #fcfce171;
  border: none;
  border-radius: 100%;
  width: 5.5vw;
  height: 5.5vw;
  margin-top: 1.6vw;
  font-family: cursive;
  font-size: 1.6vw;
  transition: all .2s;
  transform: rotate(270deg);
}

.bottom_half {
  height: fit-content;
  padding-bottom: 10vh;
  position: relative;
  top: 70vh;
}

.bottom_half--form {
  color: #000;
  background-image: linear-gradient(#fafa01f6, orange);
  border-radius: 3% / 2%;
  width: 48%;
  height: fit-content;
  padding-top: 3%;
  padding-bottom: 3%;
  position: relative;
  top: 5%;
  left: 26%;
  box-shadow: 5px 5px 10px #0d0e4488;
}

.bottom_half--form h1 {
  color: #0359b6;
  letter-spacing: .2vw;
  margin-bottom: 3%;
  font-size: 2.3vw;
}

.bottom_half--form input {
  aspect-ratio: 1 / .09;
  letter-spacing: .1vw;
  border-color: #bbb;
  border-radius: 2% / 15%;
  width: 20vw;
  margin-top: 5.5vh;
  padding-left: .5vw;
  font-size: .9vw;
}

.bottom_half--inputs {
  background-image: linear-gradient(#fff7f7, #fff4ef);
  border-radius: 2%;
  width: 41vw;
  height: fit-content;
  margin-top: 3%;
  margin-bottom: 4%;
  margin-left: 7%;
  padding: 7%;
  position: relative;
}

.bottom_half--warning {
  color: #f0f8ff;
  text-align: center;
  letter-spacing: .1vw;
  background-color: #fa7304;
  border-radius: 1% / 14%;
  padding: .8%;
  font-size: 1.2vw;
  position: relative;
  box-shadow: 2px 2px 2px #0d0e4438;
}

.bottom_half--username_explanation, .bottom_half--password_explanation, .bottom_half--email_explanation {
  letter-spacing: .2vw;
  margin-top: 10%;
  margin-bottom: 4%;
  font-size: 1.6vw;
}

.bottom_half--username_explanation {
  margin-top: 0%;
}

.username_explanation--tiny, .password_explanation--tiny, .email_explanation--tiny {
  color: #000;
  text-align: center;
  letter-spacing: .1vw;
  background-color: #f8efcf;
  border-radius: 1% / 9%;
  padding: 1.5%;
  font-size: 1vw;
  box-shadow: 2px 2px 4px #00000085;
}

.btn_password_visibility--login_bottom {
  width: 3.6%;
  top: 59.4%;
  right: 27.4%;
}

.btn--next {
  color: #f0f8ff;
  cursor: pointer;
  aspect-ratio: 1 / .4;
  background-color: #00a9dd;
  border: none;
  border-radius: 10% / 25%;
  width: 9vw;
  padding-bottom: .4vw;
  font-family: cursive;
  font-size: 2.2vw;
  transition: all .2s;
  transform: rotate(180deg);
}

.btn--next:hover {
  background-color: #3fc;
}

.bottom_half--btn_next {
  margin-top: 1.5%;
}

.forgot_password--form {
  aspect-ratio: 1 / .6;
  text-align: center;
  opacity: 0;
  z-index: -2;
  background-image: linear-gradient(#fff, #f0e2d1);
  border-radius: 2% / 3.5%;
  width: 37vw;
  padding: 2% 2% 0%;
  transition: all .8s;
  position: absolute;
  top: 20vh;
  left: 31vw;
  box-shadow: 5px 5px 20px #00000083;
}

.forgot_password--form h2 {
  letter-spacing: .2vw;
  word-spacing: .3vw;
  color: #026b79;
  font-size: 2vw;
}

.forgot_password--btn_x {
  color: #00696d;
  cursor: pointer;
  background-color: #0000;
  border: none;
  font-family: cursive;
  font-size: 2.2vw;
  transition: all .2s;
  position: absolute;
  top: -1%;
  right: 3%;
}

.forgot_password--btn_x:hover {
  color: #009baf;
}

.forgot_password--warning {
  color: #f0f8ff;
  text-align: center;
  letter-spacing: .1vw;
  background-color: #fa7304;
  border-radius: 2% / 14%;
  padding: 1.5%;
  font-size: 1.2vw;
  position: absolute;
  top: 22%;
  left: 25%;
  box-shadow: 2px 2px 2px #0d0e4438;
}

.forgot_password--explanation, .forgot_password--disclaimer {
  letter-spacing: .1vw;
  margin-top: 9%;
}

.forgot_password--explanation {
  font-size: 1.5vw;
}

#forgot_password--input_email {
  letter-spacing: .1vw;
  border-color: #bbb;
  border-radius: 2% / 15%;
  width: 60%;
  height: 10%;
  margin-top: 5%;
  padding: 2%;
  font-size: 1vw;
}

.btn--send {
  color: #f0f8ff;
  cursor: pointer;
  letter-spacing: .1vw;
  background-image: linear-gradient(#ffd51b, #f60);
  border: none;
  border-radius: 12% / 20%;
  margin-left: 3%;
  padding: 1.3%;
  font-family: cursive;
  font-size: 1vw;
  transition: all .2s;
}

.btn--send:hover {
  transform: translateY(-1px);
}

.forgot_password--disclaimer {
  font-size: 1.2vw;
}

.overlay--app_explanation {
  backdrop-filter: blur(5px);
  text-align: center;
  z-index: 4;
  z-index: 5;
  background-color: #00000060;
  flex-direction: column;
  align-items: center;
  width: 100vw;
  height: 100vh;
  display: flex;
  position: fixed;
  top: 0%;
  left: 0%;
  overflow-y: auto;
}

.overlay--app_explanation .overlay_in_app_explanation--about_this_web {
  border-radius: 2% / 1.5%;
}

.overlay--app_explanation .overlay_in_app_explanation--how_to_use {
  border-radius: 2% / .4%;
}

.overlay--app_explanation .overlay_in--app_explanation {
  background-image: linear-gradient(#fdff75, #ffac5f);
  flex-direction: column;
  align-items: center;
  width: 55vw;
  height: fit-content;
  margin: 2%;
  display: flex;
  position: relative;
}

.overlay--app_explanation .btn_x--app_explanation {
  color: #09f;
  cursor: pointer;
  background-color: #0000;
  font-family: cursive;
  font-size: 2.5vw;
  transition: all .2s;
  right: 2.5%;
}

.overlay--app_explanation .btn_x--app_explanation:hover {
  color: #6fddff;
}

.overlay--app_explanation .overlay_in_app_explanation--inside {
  background-image: linear-gradient(#fff, #f5ecec);
  border-radius: 1% / .7%;
  width: 85%;
  height: fit-content;
  margin: 5%;
  padding: 4%;
  position: relative;
}

.overlay--app_explanation .overlay_in_app_explanation--inside h1 {
  letter-spacing: .1vw;
  margin-bottom: 5%;
  font-size: 2.4vw;
}

.overlay--app_explanation .overlay_in_app_explanation--inside h2 {
  letter-spacing: .1vw;
  margin-bottom: 5%;
  font-size: 2vw;
}

.overlay--app_explanation .overlay_in_app_explanation--inside h5 {
  letter-spacing: .1vw;
  margin-top: 10%;
  font-size: 1.7vw;
}

.overlay--app_explanation .overlay_in_app_explanation--inside h5:first-child {
  margin-top: 5%;
}

.overlay--app_explanation .overlay_in_app_explanation--inside h6 {
  letter-spacing: .05vw;
  margin-top: 5%;
  font-size: 1.4vw;
}

.overlay--app_explanation .overlay_in_app_explanation--inside .p--how_to_use {
  letter-spacing: .1vw;
  text-decoration: underline red;
}

.overlay--app_explanation .overlay_in_app_explanation--inside p {
  margin-top: 5%;
  font-size: 1.4vw;
}

.overlay--app_explanation .overlay_in_app_explanation--inside img {
  width: 92%;
  margin-bottom: 3%;
}

.overlay--app_explanation .overlay_in_app_explanation--inside .image_about_this_web--goals {
  aspect-ratio: 1 / .6;
  margin-top: 6%;
}

.overlay--app_explanation .overlay_in_app_explanation--inside .image_about_this_web--contenteditable {
  aspect-ratio: 1 / .35;
}

.overlay--app_explanation .overlay_in_app_explanation--inside .image_about_this_web--rooms {
  aspect-ratio: 1 / .4;
  margin-top: 3%;
}

.overlay--app_explanation .overlay_in_app_explanation--inside .image_how_to_use--all {
  width: 102%;
  margin-top: 5%;
}

.overlay--app_explanation .overlay_in_app_explanation--inside .image_how_to_use--set_goals {
  transform: scale(.7);
}

.overlay--app_explanation .overlay_in_app_explanation--inside .image_how_to_use--create_rooms {
  margin-top: 7%;
  margin-bottom: 7%;
  transform: scale(1.15);
}

.overlay--app_explanation .overlay_in_app_explanation--inside .image_how_to_use--settings {
  margin-top: 7%;
}

.overlay--app_explanation .overlay_in_app_explanation--inside .to_set_goals_container {
  transform: translateY(10%);
}

.page--main {
  text-align: center;
  z-index: 2;
  font-family: cursive;
}

.top_section {
  aspect-ratio: 1 / .2;
  width: 100vw;
  position: relative;
}

.switch--goals_rooms {
  color: #f0f8ff;
  aspect-ratio: 1 / .3;
  border: thick solid #ddd;
  border-radius: 3% / 9%;
  flex-direction: row;
  width: 10vw;
  height: fit-content;
  display: flex;
  position: relative;
  top: 4%;
  left: 3%;
}

.switch--goals, .switch--rooms {
  letter-spacing: .1vw;
  width: 50%;
  height: 100%;
  padding: 2% 6%;
  font-size: 1.15vw;
}

.switch--goals {
  background-image: linear-gradient(#ffae00, #f60);
}

.switch--rooms {
  background-image: linear-gradient(#008cff, #00f);
}

.btn--switch {
  color: #f0f8ff;
  cursor: pointer;
  background-image: linear-gradient(#e2e2e2, #a09e9e);
  border: none;
  width: 51%;
  height: 100%;
  font-family: cursive;
  transition: all .5s;
  position: absolute;
}

.welcome {
  letter-spacing: .1vw;
  pointer-events: none;
  background-image: url("cloud.42b19650.svg");
  background-repeat: no-repeat;
  background-size: 101%;
  width: 18.5vw;
  height: 18.5vw;
  padding-top: 7.6vw;
  font-size: 1.7vw;
  position: absolute;
  top: 5%;
  left: 6%;
}

.title {
  letter-spacing: .2vw;
  word-spacing: .6vw;
  margin-top: 5%;
  font-family: cursive;
  font-size: 3.2vw;
}

.times_to_click {
  aspect-ratio: 1 / .75;
  background-image: url("fukidashi.f73c91f6.svg");
  background-repeat: no-repeat;
  background-size: 100%;
  width: 12vw;
  padding-top: 2.5%;
  font-size: 1.3vw;
  line-height: 168%;
  position: absolute;
  top: 0%;
  left: 66%;
}

.btn--set_goal, .btn--create_room {
  color: #f0f8ff;
  cursor: pointer;
  background: 0 0 / contain no-repeat;
  border: none;
  height: 10%;
  padding-top: 8%;
  font-family: cursive;
  font-size: 1.5vw;
  transition: all .2s;
  position: absolute;
  top: 20%;
}

.btn--set_goal:hover, .btn--create_room:hover {
  transform: translateY(-.3vh);
}

.btn--set_goal {
  color: #f0f8ff;
  cursor: pointer;
  background: url("sun.3e3763dd.svg") 0 0 / 105% no-repeat;
  border: none;
  width: 8%;
  font-family: cursive;
  transition: all .2s;
  left: 79%;
}

.btn--create_room {
  color: #f0f8ff;
  cursor: pointer;
  background: url("house.912daae6.svg") 0 0 / 105% no-repeat;
  border: none;
  width: 7%;
  font-family: cursive;
  transition: all .2s;
  left: 88%;
}

.btn--logout {
  color: #000;
  cursor: pointer;
  letter-spacing: .1vw;
  background-color: #ccf9ff8a;
  border: none;
  border-radius: 3%;
  padding: .6%;
  font-family: cursive;
  font-size: 1vw;
  transition: all .2s;
  position: absolute;
  top: 4%;
  right: 2%;
}

.btn--logout:hover {
  transform: translateY(-.3vh);
}

.days_counter--container {
  grid-template-columns: repeat(2, 1fr);
  gap: 5vh 1vw;
  margin-top: 3vh;
  display: grid;
}

.start_explanation {
  letter-spacing: .2vw;
  color: #f0f8ff;
  margin-top: 5%;
  font-size: 2.5vw;
}

.card {
  aspect-ratio: 1 / .6;
  z-index: 2;
  background-color: #fffd78;
  border-radius: 2.5% / 3.5%;
  width: 45vw;
  margin-left: 2vw;
  position: relative;
}

.title--outer {
  width: 64%;
  margin-bottom: 3%;
  position: relative;
  top: -20%;
  left: 13%;
}

.title--outer h2 {
  white-space: nowrap;
  text-overflow: ellipsis;
  letter-spacing: .2vw;
  font-size: 2vw;
  overflow: hidden;
}

.room_id_container {
  white-space: nowrap;
  flex-direction: row;
  gap: 4.5%;
  width: fit-content;
  height: 7%;
  display: flex;
  position: absolute;
  top: 2%;
  left: 3%;
}

.room_id_container .room_id {
  font-size: 1.2vw;
}

.room_id_container .btn--room_id_copy {
  color: #f0f8ff;
  cursor: pointer;
  background: url("copy.400dd357.svg") center / 115% no-repeat;
  border: none;
  width: 2.4vw;
  margin-top: .7%;
  font-family: cursive;
  transition: all .2s;
}

.room_id_container .room_id--copied {
  background-image: url("check.611c7d13.svg");
  background-size: contain;
}

.room_id_container .copied_explanation {
  letter-spacing: .03vw;
  background-color: #83ffe0a1;
  border-radius: 10% / 25%;
  padding: 1%;
  font-size: 1.1vw;
  position: absolute;
  top: 3%;
  right: -21%;
}

.usernames_container {
  letter-spacing: .1vw;
  white-space: nowrap;
  text-overflow: ellipsis;
  opacity: 0;
  pointer-events: none;
  background: url("fukidashiYellow.efffdfe4.svg") 0 0 / cover;
  width: 80%;
  height: 35%;
  padding: 4% 4% 4% 8%;
  font-size: 1.3vw;
  transition: all .5s;
  position: relative;
  top: -33%;
  left: 15%;
  overflow: hidden;
}

.usernames_container p {
  margin-bottom: 1%;
}

.usernames_container .usernames {
  color: #009baf;
}

.btn--usernames {
  color: #f0f8ff;
  cursor: pointer;
  letter-spacing: .1vw;
  background-image: linear-gradient(#ccc, gray);
  border: none;
  border-radius: 100%;
  padding: 1.2%;
  font-family: cursive;
  font-size: 1.2vw;
  transition: all .2s;
  position: absolute;
  top: 3%;
  right: 3%;
}

.btns--edit_delete {
  cursor: auto;
  flex-direction: row;
  gap: 10%;
  width: 23%;
  display: flex;
  position: absolute;
  right: 0%;
}

.btns_edit_delete--days_counter {
  top: 19%;
}

.btn--edit, .btn--finish, .btn--delete {
  letter-spacing: .1vw;
  border: none;
  border-radius: 8% / 14%;
  padding: 4%;
  font-size: 1vw;
}

.btn--edit:hover, .btn--finish:hover, .btn--delete:hover {
  transform: translateY(-1px);
}

.btn--edit, .btn--finish {
  color: #f0f8ff;
  cursor: pointer;
  background-image: linear-gradient(orange, #f60);
  font-family: cursive;
  transition: all .2s;
}

.btn--delete {
  color: #f0f8ff;
  cursor: pointer;
  background-image: linear-gradient(#00c3ff, #09f);
  font-family: cursive;
  transition: all .2s;
}

.bottom_content--days_counter {
  flex-direction: row;
  width: 100%;
  height: 55%;
  display: flex;
  position: relative;
  top: -7%;
  left: 5%;
}

.bottom_content--days_counter p {
  letter-spacing: .1vw;
  margin-top: 13%;
  margin-left: 4%;
  font-size: 1.5vw;
}

.comments, .to_do_lists {
  text-align: left;
  cursor: auto;
  letter-spacing: .1vw;
  padding: 4%;
  font-size: 1.5vw;
  overflow-y: scroll;
}

.comments--days_counter, .to_do_lists--days_counter {
  background-image: linear-gradient(#fff, #fdf6f6);
  width: 90%;
  height: 60%;
  margin-left: 5%;
  position: relative;
  top: -18%;
}

.checkbox_container input {
  width: 1.3vw;
  height: 1.3vw;
}

.details--outer {
  background-color: #fac104;
  border-radius: 3.3% / 6%;
  width: 55%;
  height: 80%;
  margin-left: 5%;
  overflow: hidden;
}

.details {
  letter-spacing: .2vw;
  text-align: left;
  white-space: nowrap;
  scrollbar-color: #fe0 orange;
  scroll-behavior: smooth;
  flex-direction: column;
  width: 100%;
  height: 100%;
  padding: 8%;
  font-size: 1.5vw;
  line-height: 175%;
  display: flex;
  overflow: auto hidden;
}

.remaining_days--outer {
  text-align: center;
  background-color: #fff5c7;
  border-radius: 7%;
  width: 20%;
  height: 55%;
  overflow: hidden;
}

.remaining_days {
  letter-spacing: .3vw;
  scrollbar-width: thin;
  scrollbar-color: #fffaed #ffea2c;
  scroll-behavior: smooth;
  width: 100%;
  height: 100%;
  padding-top: 23%;
  font-size: 3vw;
  overflow: auto hidden;
